@import url(https://fonts.googleapis.com/css?family=Nunito:200,300,regular,500,600,700,800,900,200italic,300italic,italic,500italic,600italic,700italic,800italic,900italic);

html {
  	box-sizing: border-box;
}

*,
*::before,
*::after {
  	box-sizing: inherit;
}

body {
  	color: #212121;
	font-family: 'Nunito', sans-serif;
}

a {
	color: #212121;
	text-decoration: none;
	opacity: 1;
	transition: opacity .2s ease-in-out;
}

a:hover {
  	opacity: 0.7;
}

.flex {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.container {
	margin: 0 auto;
	padding: 0 15px;
	max-width: 1140px;
}

.btn {
	background: #FF8000;
	border-radius: 10px;
	color: #fff;
	border: none;
	outline: none;
	padding: 15px 45px;
	font-size: 17px;
	opacity: 1;
	cursor: pointer;
	transition: opacity .2s ease-in-out;
}

.btn:hover {
	opacity: .85;
}

.title {
	font-size: 32px;
	padding: 0;
	margin: 0;
	font-weight: bold;
	span {
		color: #BB2649;
	}
}

.overflow {
	overflow: hidden;
}

.hero-section {
	background: url('../img/hero-section-bg.jpg') center no-repeat;
	background-size: cover;
    background-position: center;
	border-radius: 25px;
	&__title {
		margin: 75px 0 !important;
	}
	&__wrap {
		position: relative;
		padding: 20px 0;
		display: flex;
		align-items: center;
		flex-direction: column;
	}
	&__breadcrumbs {
		display: flex;
		gap: 5px;
		align-items: center;
		a, span {
			color: #fff;
			font-size: 13px;
			opacity: 0.5;
		}
		a:hover {
			opacity: 0.4;
		}
	}
	&__line {
		position: absolute;
		width: 217px;
		height: 10px;
		bottom: -5px;
		background: #FF8000;
		border-radius: 10px;
	}
}

@media (max-width: 992px) {
	.hero-section {
		&__line {
			width: 180px;
		}
		&__breadcrumbs {
			a, span {
				font-size: 12px;
			}
			gap: 4px;
		}
		&__title {
			margin: 60px 0px !important;
		}
	}
	.title {
		font-size: 28px;
	}
}

@media (max-width: 576px) {
	.title {
		font-size: 24px;
	}
}